import React from 'react';
import FlipMove from 'react-flip-move';
import Tile from './Tile';
import Toggle from './Toggle';

class Guts extends React.Component {
  constructor(props) {
    console.log(props)
    super(props);
    this.state = {
      order: 'desc',
      head: props.head,
      cards: props.cards
    }
    this.toggleSort = this.toggleSort.bind(this);
  }


 toggleSort() {
    const sortAsc = (a, b) => a.id - b.id;
    const sortDesc = (a, b) => b.id - a.id;

    this.setState({
      order: (this.state.order === 'asc' ? 'desc' : 'asc'),
      cards: this.state.cards.sort(
        this.state.order === 'asc' ? sortDesc : sortAsc
      )
    });
  }

  componentWillMount() {
      this.toggleSort()
      this.setState({head:this.state.head[0]})
  }

  render() {
    return (
      <div>
        <Toggle clickHandler={this.toggleSort}
                text="Button" />
            <div>
                <Tile 
                    classString="hero-container"
                    key={this.state.head.id}
                    details="All"
                    data={this.state.head.data}
                    />
            </div>
            <div>
              <FlipMove
                  staggerDurationBy="30"
                  duration={500}
                  enterAnimation={this.state.enterLeaveAnimation}
                  leaveAnimation={this.state.enterLeaveAnimation}
                  typeName="ul"
                >
                {this.state.cards.map((item,index)=>{
                   return <Tile 
                            key={item.id}
                            classString="tile-container"
                            details={item.name.split("|")[0]}
                            data={item.data}
                            />
               })}
              </FlipMove>
        </div>
      </div>
      )
  }
}

export default Guts;