import React from 'react';
import Fever from './Fever';
import CPULineChart from './CPULineChart';

class Tile extends React.Component {
  constructor(props) {
    super(props);
  }

  render() {
    return (
      <li className={this.props.classString}>
        <h1>Trend</h1>
        <Fever data={this.props.data}/>
        <h3>{this.props.details}</h3>
      </li>
      )
  }
}

export default Tile