import React, { useEffect, useState } from "react";
import Guts from './Guts';

const Fetcher = () => {  
  const [cards, setCards] = useState([])
  const [head, setHead] = useState([])
  const fetchData = () => {
    fetch("http://localhost:3001/dataGroupedStackedP")
      .then(response => {
        return response.json()
      })
      .then(data => {
        const keys = Object.keys(data.data[0]["asp_quotes"])
                let finalData = {}
                let finalArray = []

                keys.forEach((entry,i) => {
                  let pod = {}
                  pod["name"] = entry
                  pod["id"] = i + 1
                  pod["data"] = []
                  data.data.forEach((ent)=>{
                    let info = {}
                    info["date"] = ent["price_requests.created_month"]["value"]
                    info["high"] = ent["asp_quotes"][entry]["value"]
                    pod["data"].push(info)
                  })
                  finalArray.push(pod)
                })
                const head = finalArray.filter(en => en.name == "$$$_row_total_$$$")

                const items = finalArray.filter((en) => {
                  return en.name != "$$$_row_total_$$$"})

                setCards(items)
                setHead(head)
      })
  }

  useEffect(() => {
    fetchData()
  }, [])

  return (
    <div className="tile-container-container">
      {cards.length > 0 &&
        <Guts head={head} cards={cards}/>
      }
    </div>
  )
}

export default Fetcher